.panel-state-collapsed {

  &.panel {

    &-position-bottom {
      right: var(--workflow-editor-width);
      left: var(--activity-picker-width);
    }

    &panel-position-top {
      width: 0;
    }

    &panel-position-left {
      height: 0;
    }
  }

  // .panel-content-container {
  //   left: 0;
  //   top: 0;
  //   display: none;
  // }
}

.panel-position-bottom {
  right: var(--workflow-editor-width) !important;
  left: var(--activity-picker-width) !important;
}
